<template>
  <div class="main_content">
    <Index-header></Index-header>
    <div class="share_bg" ref="imageDom">
      <img class="share_bg1" src="../../assets/images/mine/share_bg.png" />
      <div class="share_bg2">
        <img class="share_text" src="../../assets/images/mine/share_text.png" />
        <div class="share_codebg_bg">
          <img class="share_codebg" src="../../assets/images/mine/share_codebg.png" />
          <label class="addCode">邀请码</label>
          <div class="addCode2">
            <span v-for="item in idcodes" :key="item">{{item}}</span>
          </div>
          <img id="wxcode" class="share_code" src="../../assets/images/wx_code.png" />
        </div>
      </div>
      <img class="share_gold" src="../../assets/images/mine/share_gold.png" />
    </div>
    <div class="download-box">
      <button class="copy_btn" @click="copyLink"
          :data-clipboard-text="registerUrl">复制邀请链接</button>
      <!-- <button @click="saveImage">保存图片</button> -->
    </div>

    <div class="mask"></div>
    <div id="logout" class="dialog">
      <img id="shareImg" class="shareImg" src="../../assets/images/wx_code.png" />
    </div>

  </div>
</template>

<script>
import Header from "../common/Header";
import axios from "axios";
import Vue from "vue";
import commonURL from "../../assets/js/ajax";
import { Base64 } from 'js-base64';
import {  } from "@/api/public.js";
import $ from "../../assets/js/jquery-3.3.1.min";
import wx from "weixin-js-sdk";
import moment from "moment";
import Clipboard from 'clipboard';
import QRCode from 'qrcode';
import html2canvas from "html2canvas";

export default {
  name: "share",
  data() {
    return {
      loginData: {}, //登陆的数据
      registerUrl:'',
      idcodes:[]
    };
  },
  components: {
    "Index-header": Header
  },
  created() {
    this.$store.state.title = "邀请好友";
    this.$store.state.linkName = "mine";
    var url = window.location.href.split('share')[0];
    console.log(url);
    if(!!localStorage.getItem("loginData")){
      this.loginData = JSON.parse(localStorage.getItem("loginData"));
      this.registerUrl = url + 'register?idcode=' + this.loginData.idcode;
      this.idcodes = this.loginData.idcode.split("");
    }
  },
  mounted() {
    document.querySelector('html').setAttribute('style', 'background-color:#F5F5F5')
    QRCode.toDataURL(this.registerUrl)
      .then(url => {
        document.getElementById('wxcode').setAttribute( 'src', url);
      })
      .catch(err => {
        console.error(err)
      })
  },
  methods: {
     // 复制
    copyLink() {
      var clipboard = new Clipboard(".copy_btn");
      clipboard.on("success", e => {
        this.$message.success("已复制好，可贴粘。");
        // 释放内存
        clipboard.destroy();
      });
      clipboard.on("error", e => {
        // 不支持复制
        this.$message.success("该浏览器不支持自动复制");
        // 释放内存
        clipboard.destroy();
      });
    },
    saveImage(){
       html2canvas(this.$refs.imageDom).then(canvas => {
        // 转成图片，生成图片地址
        this.imgUrl = canvas.toDataURL("image/png");
        $(".mask").show();
        $("#logout").show();
        document.getElementById('shareImg').setAttribute( 'src', this.imgUrl);
        this.downloadIamge($('#shareImg').attr('src'), 'pic')
        setTimeout(() => {
          $(".mask").hide();
          $("#logout").hide();
        }, 2000);
      });
    },
    downloadIamge(imgsrc, name) {//下载图片地址和图片名
      var image = new Image();
      // 解决跨域 Canvas 污染问题
      image.setAttribute("crossOrigin", "anonymous");
      image.onload = function() {
        var canvas = document.createElement("canvas");
        canvas.width = image.width;
        canvas.height = image.height;
        var context = canvas.getContext("2d");
        context.drawImage(image, 0, 0, image.width, image.height);
        var url = canvas.toDataURL("image/png"); //得到图片的base64编码数据

        var a = document.createElement("a"); // 生成一个a元素
        var event = new MouseEvent("click"); // 创建一个单击事件
        a.download = name || "photo"; // 设置图片名称
        a.href = url; // 将生成的URL设置为a.href属性
        a.dispatchEvent(event); // 触发a的单击事件
      };
      image.src = imgsrc;
    },
  },
  beforeDestroy () {
    document.querySelector('html').removeAttribute('style')
  }
};
</script>

<style scoped>
.main_content{
    height: 100%;
    position: relative;
}
.share_bg{
  width: 100%;
  position: relative;
}
.share_bg1{
  display: block;
  width: 100%;
}
.share_bg2{
  width: 80%;
  position: absolute;
  top: 20px;
  left: 10%;
}
.share_text{
  display: block;
  width: 90%;
  margin-left: 5%;
}
.share_codebg_bg{
  width: 100%;
  position: relative;
}
.share_codebg{
  display: block;
  width: 100%;
}
.share_code{
  display: block;
  width: 60%;
  position: absolute;
  bottom: 58px;
  left: 20%;
}
.share_gold{
  position: absolute;
  top: 80px;
  width: 86%;
  left: 7%;
  display: block;
}
.addCode{
  position: absolute;
  top: 30px;
  width: 50%;
  left: 25%;
  color: #000;
  font-size: 18px;
}
.addCode2{
  position: absolute;
  top: 62px;
  width: 80%;
  left: 10%;
  color: #FC6A4F;
  font-size: 24px;
}
.addCode2 span{
  background: #EFEFEF;
  padding: 0px 4px;
  margin: 0px 4px;
}
.download-box{
    width: 100%;
    display: flex;
    justify-content: space-evenly;
    margin-top: 5px;
}
button{
    width: 150px;
    height: 40px;
    line-height: 40px;
    border: none;
    border-radius: 20px;
    text-align: center;
    color: #fff;
    font-family: 'PingFangSC-Medium';
    font-size: 16px;
    background: -moz-linear-gradient(left, #0D7CFA 0%, #30ACFC 100%);
    background: -webkit-gradient(
      linear,
      top left,
      top right,
      color-stop(0%, #0D7CFA),
      color-stop(100%, #30ACFC)
    );
    background: -webkit-linear-gradient(left, #0D7CFA 0%, #30ACFC 100%);
    background: -o-linear-gradient(left, #0D7CFA 0%, #30ACFC 100%);
    background: -ms-linear-gradient(left, #0D7CFA 0%, #30ACFC 100%);
    background: linear-gradient(to right, #0D7CFA 0%, #30ACFC 100%);
}
.shareImg{
  width: 100%;
}
</style>



